resaron al programa Mi familia por cumplir con el criterio de ser familias con niños, niñas o adolescentes en situación de discapacidad. En las familias que ingresan por los demás criterios de focalización también es posible encontrar personas en situación de discapacidad, las cuales se identifican a través de los instrumentos de caracterización y diagnóstico. ")])])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-4"},[_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"bg_07 p-4"},[_c('div',{staticClass:"row justify-content-end align-items-end"},[_c('div',{staticClass:"col-lg-8 d-flex align-items-center"},[_c('p',{staticClass:"mb-4"},[_vm._v("Los listados de las familias y de las personas beneficiarias del programa Mi familia durante la vigencia 2020 se cruzaron con la Base maestra del DNP, permitiendo estimar las condiciones socioeconómicas previas a la pandemia y el acceso a programas sociales de estas familias. Como resultado del cruce mencionado se encontró que el 46 % de las familias beneficiarias de Mi familia en 2020 estaban registradas en el Sisbén IV, es decir, contaban con información socioeconómica actualizada, pero previa a la pandemia. Para el caso particular de las familias con NNAcD se encontró información actualizada de 1.254 de ellas.")])]),_c('div',{staticClass:"col-lg-4"},[_c('figure',{attrs:{"data-aos":"zoom-in-up"}},[_c('img',{staticClass:"img-t m-auto",attrs:{"src":require("@/assets/curso/tema1/img10.svg"),"alt":""}})])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',{staticClass:"mb-5"},[_vm._v("A partir del análisis de los registros de Sisbén IV para estas familias se encontró que el 25 % de los NNAcD no están vinculados al sistema educativo (medido a través de la asistencia escolar) evidenciando una brecha importante al compararlos con niños, niñas o adolescentes de las demás familias